@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  overflow:hidden;
  margin: 0;
  padding: 0;
  height: 100vh;
  font-family: 'Ubuntu', Arial, Helvetica, sans-serif
}

.col{
    background: #eee;
    color:black;
    border: 1px #921029 solid;
    border-left: none; border-bottom: none;
    box-sizing: border-box;
    cursor: pointer;
    width: 5rem; height: 4rem;
    display:flex;
    align-items: center;
    font-size: 1.4rem;
    justify-content: center;
}

.zero{
    width: 10rem;
}

.operator{
    background: #F2274F;
    color:white;
    font-size: 2rem;
}

.special{
    background: #444;
    color:white;
}
#display-wrapper{
    width: 20rem;
    height: 5rem;
    border-right: 1px #921029 solid;
    background: black;
    position: relative;
}

#display{
    color:white;
    font-size: 2rem;
    position: absolute;
    right: 0;
    bottom: 0;
}

#input{
    color:white;
    background: black;
    border: black;
    text-align: right;
    position: absolute;
    right: 0;
    width: 100%;
    font-size: 1.2rem;
    overflow: auto;
}

#input:focus{
    outline: none;
}
h1{
    padding: 0;
    margin: 0;
}

#main{
    height: 100vh;
    position: relative;
}

#page-title{
    text-align: center;
    font-weight: bold;
    padding: 2rem 0;
}

.container{
    width: 20rem;
    margin: 0 auto;
    height: auto;
    border: 1px #921029 solid;
    border-right: none;
}

.row{
    display:flex;
    width: 20rem;
}
